<template>
    <div id="app">
<!--        <Header></Header>-->
        <router-view></router-view>
        <Footer></Footer>
    </div>
</template>

<script>
    // import Header from './components/Header'
    import Footer from "./components/Footer";
    export default {
        name: 'app',
        components: {
            Footer
        },
        data(){
            return {}
        },
        watch: {
            $route: {
                handler: function(route){
                    let path = route.path || '/';
                    this.checkPath(path);
                }
            }
        },
        methods: {
            checkPath(path){
                const { commit } = this.$store;
                if (/home/.test(path)) {
                    commit('UPDATE_TAB_BAR_ACTIVE', 0)
                    commit('UPDATE_HEADER_TITLE', '免费书库')
                }
                if (/tree_select/.test(path)) {
                    commit('UPDATE_TAB_BAR_ACTIVE', 1)
                    commit('UPDATE_HEADER_TITLE', '分类')

                }
                if (/bookshelf/.test(path)) {
                    commit('UPDATE_TAB_BAR_ACTIVE', 2)
                    commit('UPDATE_HEADER_TITLE', '我的书架')

                }
            }
        }
    }
</script>

<style lang="scss">
    #app {
        background: rgb(240, 240, 240);
    }
</style>
