import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/css/public.css'
import './assets/js/aliRem'
Vue.config.productionTip = false

import { NavBar, Tabbar, TabbarItem, TreeSelect } from 'vant'

Vue
    .use(NavBar)
    .use(TreeSelect)
    .use(Tabbar).use(TabbarItem)


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
