<template>
    <div class="home">
        <van-nav-bar  title="免费书库" />
        <div>123</div>
    </div>
</template>

<script>
    import { getHotRecommendAPI } from '@/services/bookControllers'
    export default {
        name: "Home",
        data(){
            return {
                hotRecommend: []
            }
        },
        beforeRouteEnter(to, from, next){
            const { path } = from;
            next(vm => {
                vm.$store.commit('UPDATE_PREV_PATH', path)
            })
        },
        methods: {},
        mounted() {
            getHotRecommendAPI().then(res => {
                if (res.status === 200) {
                    this.hotRecommend = res.msg;
                }
            }).catch(err => {
                console.log(err)
            })
        }
    }
</script>

<style lang="scss" scoped>
    .home{
        background: aquamarine;
    }
</style>