import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      headerTitle: '免费书库',
      footerShow: true,
      tabBarActive : 0,
      prevPath: '/',
      loading: false
  },
  mutations: {
      UPDATE_HEADER_TITLE: (state, payload) => {
          state.headerTitle = payload
      },
      UPDATE_TAB_BAR_ACTIVE: (state, payload) => {
          state.tabBarActive = payload
      },
      UPDATE_PREV_PATH: (state, payload) => {
          state.prevPath = payload
      },
      UPDATE_LOADING: (state, payload) => {
          state.loading = payload
      }
  },
  actions: {
      UPDATE_HEADER_TITLE: ({commit}, payload) => {
          commit('UPDATE_HEADER_TITLE',payload)
      },
      UPDATE_LOADING: ({commit}, payload) => {
          console.log('更新loading')
          commit('UPDATE_LOADING',payload)
      }
  },
  getters: {
      headerTitle: (state) => state.headerTitle,
  }
})
